import React from 'react'
import { CardContent, CardHeader, Divider, Grid, TextField } from '@material-ui/core'

const SellerBankingForm = (props) => {
  const sellerBankingDetailsFormFields = {
    bankName: {
      type: 'text',
      placeholder: 'Bank Name',
      name: 'bankName'
    },
    accountTitle: {
      type: 'text',
      placeholder: 'Account Title',
      name: 'accountTitle'
    },
    accountNumber: {
      type: 'text',
      placeholder: 'Account Number',
      name: 'accountNumber'
    },
    routingNumber: {
      type: 'text',
      placeholder: 'Routing Number',
      name: 'routingNumber'
    }
  }

  return (
    <React.Fragment>
      <CardHeader title="Banking Details" />
      <Divider />
      <CardContent>
        <Grid
          container
          spacing={4}
        >
          {Object.keys(sellerBankingDetailsFormFields).map(fieldKey => {
            const field = sellerBankingDetailsFormFields[fieldKey]
            return (
              <Grid
                item
                md={6}
                xs={12}
              >
                <TextField
                  disabled={props.loading}
                  fullWidth
                  label={field.placeholder}
                  name={field.name}
                  onChange={props.handleChange}
                  type={field.type}
                  value={props.values[field.name]}
                  variant="outlined"
                />
              </Grid>
            )
          })}
        </Grid>
      </CardContent>

    </React.Fragment>
  )
}

export default SellerBankingForm
