import React, { Component } from 'react';

class Map extends Component {
  googleMapRef = React.createRef();
  state = {
    marker: null,
    circle:null
  };

  componentDidMount() {
    const googleMapScript = document.createElement('script');
    googleMapScript.src = `https://maps.googleapis.com/maps/api/js?key=${this.props.apiKey}&libraries=places`;
    document.body.appendChild(googleMapScript);

    googleMapScript.addEventListener('load', ()=>{
      this.googleMap = this.createGoogleMap();
      this.setState({marker: this.marker})
    });


  }
  componentWillReceiveProps(nextProps) {
    if(this.props !== nextProps){
      if(this.props.values.radius !== nextProps.values.radius){
        if(this.state.marker){
          if(nextProps.values.radius === 0){
            this.removeCircleMap()
          }else {
            let marker = this.state.marker;
            let circle = this.state.circle;
            circle.setMap(null)
            circle = this.createCircle(marker, nextProps.values.radius)
            this.setState({
              marker: marker,
              circle: circle
            })
          }
        }
      }
    }
  }

  removeCircleMap = ()=>{
    let marker = this.state.marker;
    let circle = this.state.circle;
    marker.setMap(null);
    circle.setMap(null);
    this.setState({
      marker: marker,
      circle: circle
    })
  }

  createGoogleMap = () => {
    const map = new window.google.maps.Map(this.googleMapRef.current, {
      zoom: 16,
      center: {
        lat: 43.642567,
        lng: -79.387054,
      },
      disableDefaultUI: true,
    });
    map.addListener('click', (event) => {
      let latLng = event.latLng;
      if(!this.state.marker){
        let marker = this.createMarker(latLng)
        let circle = this.createCircle(marker)
        this.setState({
          marker: marker,
          circle: circle
        })
        this.props.handleRegion(marker.getPosition());
      }else{
        let marker = this.state.marker;
        let circle = this.state.circle
        circle.setMap(null)
        marker.setPosition( latLng );
        circle = this.createCircle(marker)
        this.setState({
          marker: marker,
          circle: circle
        })
        this.props.handleRegion(marker.getPosition());
      }

    });
    return map
  };

  createMarker = (latLng) => {
    return new window.google.maps.Marker({
      position: latLng,
      map: this.googleMap
    });
  };

  createCircle = (marker=this.marker, radius=this.props.values.radius)=>{
    return new window.google.maps.Circle({
      strokeColor: '#FF0000',
      strokeOpacity: 0.8,
      strokeWeight: 2,
      fillColor: '#FF0000',
      fillOpacity: 0.35,
      map: this.googleMap,
      center: marker.getPosition(),
      radius: radius*1000
    });
  }


  render() {
    const styles = {
      width: this.props.width,
      height: this.props.height
    }
    return (
      <div
        id="google-map"
        ref={this.googleMapRef}
        style={styles}
      />
    );
  }
}

export default Map;
