import { axios, apiUrls } from 'config';

export class OperationRegions {
  static getRegionsList(config) {
    const { state, setState, headers } = config;
    Promise.all([
      axios({
        method: apiUrls.regions.count.method,
        url: apiUrls.regions.count.url,
        headers: headers
      }),
      axios({
        method: apiUrls.regions.get.method,
        url: apiUrls.regions.get.url,
        params: {
          filter: {
            where: {},
            fields: {
              title: true,
              radius: true,
              center: true,
              description: true,
              searchRadius: true
            },
            offset: state.offset,
            limit: state.limit,
            skip: state.offset
          }
        },
        headers: {
          ...headers
        }
      })
    ])
      .then(([count, list]) => {
        setState({
          count: count.data.count,
          regions: list.data,
          pages: Math.ceil(count.data.count / state.limit)
        });
        console.log(count.data.count, list.data);
      })
      .catch(([countErr, listErr]) => {
        console.log(countErr.response, listErr.response);
      });
  }

  static createRegion(config) {
    const {
      data,
      headers,
      defaultConfig,
      setConfig,
      defaultValues,
      setValues
    } = config;
    const options = {
      method: apiUrls.regions.post.method,
      url: apiUrls.regions.post.url,
      data: data,
      headers: headers
    };
    axios(options)
      .then(response => {
        console.log('Success', response.data);
        setConfig(defaultConfig);
        setValues(defaultValues);
      })
      .catch(err => {
        console.log('Error', err.response, err);
      });
  }

  static deleteRegion(config) {
    const { state, setState, headers, id } = config;
    const changeState = id => {
      let newRegions = state.regions.filter(item => item.title !== id);
      setState({
        regions: newRegions
      });
    };
    const options = {
      method: apiUrls.regions.delete_by_id.method,
      url: apiUrls.regions.delete_by_id.getAbsoluteUrl(id),
      headers: headers
    };
    axios(options)
      .then(response => {
        console.log('Success', response.data);
        changeState(id);
      })
      .catch(err => {
        console.log('Error', err.response, err);
      });
    // Delete Config
  }

  static getRegion(config) {
    const { id, headers, success, error } = config;
    const options = {
      method: apiUrls.regions.get_by_id.method,
      url: apiUrls.regions.get_by_id.getAbsoluteUrl(id),
      headers: headers
    };
    axios(options)
      .then(response => {
        console.log('Success', response.data);
        success(response.data);
      })
      .catch(err => {
        error(err);
        console.log('Error', err.response, err);
      });
  }

  static updateRegion(config) {
    const { id, data, headers, success, error } = config;
    const options = {
      method: apiUrls.regions.patch_by_id.method,
      url: apiUrls.regions.patch_by_id.getAbsoluteUrl(id),
      headers: headers,
      data:data
    };
    axios(options)
      .then(response => {
        success(response.data);
      })
      .catch(err => {
        error(err);
      });
  }
}
