import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import {
  Divider,
  colors,
  CardActions,
  Card,
  CardHeader,
  Grid,
  TextField,
  Button,
  InputAdornment
} from '@material-ui/core';
import { creds } from 'config';

import { Page } from 'components';
import { Header } from './components';

import { regions } from '../utils';

import Map from './components/Map';
import { connect } from 'react-redux';

const useStyles = makeStyles(theme => ({
  root: {
    width: theme.breakpoints.values.lg,
    maxWidth: '100%',
    margin: '0 auto',
    padding: theme.spacing(3)
  },
  divider: {
    backgroundColor: colors.grey[300]
  },
  content: {
    marginTop: theme.spacing(3)
  },
  marginLeft: {
    marginLeft: theme.spacing(2)
  }
}));

const CreateRegion = props => {
  const classes = useStyles();
  const defaultValues = {
    description: '',
    radius: 0,
    center: null,
    searchRadius: 0
  };

  const defaultConfig = {
    description: {
      md: 5,
      xs: 12
    },
    radius: {
      md: 3,
      xs: 12
    },
    searchRadius: {
      md: 3,
      xs: 12
    },
    button: null
  };

  const [values, setValues] = useState(defaultValues);
  const [config, setConfig] = useState(defaultConfig);

  const handleChange = event => {
    event.persist();
    if (event.target.name === 'radius') {
      if (Number(event.target.value) > 10) {
        return null;
      }
    }
    if (event.target.name === 'searchRadius') {
      if (Number(event.target.value) > 10) {
        return null;
      }
    }

    setValues({
      ...values,
      [event.target.name]:
        (event.target.name === 'radius' || event.target.name === 'searchRadius')
          ? Number(event.target.value)
          : event.target.value
    });
  };

  const handleRegion = center => {
    setValues({
      ...values,
      center: center.toJSON()
    });
  };
  useEffect(() => {
    if (
      values.description &&
      values.description !== '' &&
      values.radius &&
      values.radius !== 0 &&
      values.center &&
      values.radius > values.searchRadius
    ) {
      console.log(values);
      setConfig({
        ...config,
        description: {
          md: 4,
          xs: 12
        },
        radius: {
          md: 3,
          xs: 12
        },
        searchRadius: {
          md: 3,
          xs: 12
        },
        button: (
          <Grid
            item
            md={2}
            xs={12}
          >
            <Button
              size="large"
              type="submit"
              variant="contained"
            >
              Submit
            </Button>
          </Grid>
        )
      });
    } else {
      setConfig(defaultConfig);
    }
  }, [values]);

  const createRegionApi = event => {
    event.preventDefault();
    regions.createRegion({
      data: {
        ...values,
        center: {
          latitide: values.center.lat,
          longitude: values.center.lng
        }
      },
      headers: props.auth.headers,
      setConfig: data => setConfig(data),
      defaultConfig: defaultConfig,
      setValues: data => setValues(data),
      defaultValues: defaultValues
    });
  };

  return (
    <Page
      className={classes.root}
      title="Settings"
    >
      <Header
        description="Create Operation Region"
        title="Operation Regions"
      />
      <Divider className={classes.divider} />
      <div className={classes.content}>
        <Card>
          <CardHeader title="Configurations" />
          <form onSubmit={createRegionApi}>
            <Grid
              className={classes.marginLeft}
              container
              spacing={4}
            >
              <Grid
                item
                md={config.description.md}
                xs={config.description.xs}
              >
                <TextField
                  fullWidth
                  label="Description"
                  name="description"
                  onChange={handleChange}
                  required
                  value={values.description}
                  variant="outlined"
                />
              </Grid>
              <Grid
                item
                md={config.radius.md}
                xs={config.radius.xs}
              >
                <TextField
                  fullWidth
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">Km</InputAdornment>
                    )
                  }}
                  label="Radius"
                  name="radius"
                  onChange={handleChange}
                  type="number"
                  value={values.radius}
                  variant="outlined"
                />
              </Grid>
              <Grid
                item
                md={config.searchRadius.md}
                xs={config.searchRadius.xs}
              >
                <TextField
                  fullWidth
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">Km</InputAdornment>
                    )
                  }}
                  label="Search Radius"
                  name="searchRadius"
                  onChange={handleChange}
                  type="number"
                  value={values.searchRadius}
                  variant="outlined"
                />
              </Grid>
              {config.button}
            </Grid>
          </form>
        </Card>
        <Card>
          <CardHeader title="Google Map" />
          <Map
            apiKey={creds.GOOGLE_MAP_API}
            handleRegion={handleRegion}
            height={400}
            values={values}
            width="100%"
          />
          <Divider />
          <CardActions />
        </Card>
      </div>
    </Page>
  );
};

CreateRegion.propTypes = {
  history: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired
};

const mapStateToProps = state => {
  return {
    auth: state.session.authentication
  };
};

export default connect(mapStateToProps)(CreateRegion);
