import React, { useState } from 'react'
import clsx from 'clsx'
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/styles'
import {
  Button,
  Card,
  CardActions,
  Divider,
  colors,
  LinearProgress
} from '@material-ui/core'
import UserForm from './UserForm'
import SellerInfoForm from './SellerInfoForm'
import SellerBankingForm from './SellerBankingForm'
import Alert from 'components/Alert'
import * as actions from '../../../../../../../actions'
import { connect } from 'react-redux'

const useStyles = makeStyles(theme => ({
  root: {},
  saveButton: {
    color: theme.palette.white,
    backgroundColor: colors.green[600],
    '&:hover': {
      backgroundColor: colors.green[900]
    }
  }
}))

const UserCreateForm = props => {
  const { className, ...rest } = props

  const classes = useStyles()
  const [values, setValues] = useState({

  })

  const handleChange = event => {
    event.persist()

    setValues({
      ...values,
      [event.target.name]:
        event.target.type === 'checkbox'
          ? event.target.checked
          : event.target.value
    })
  }
  const getValue = (key) => {
    if (values[key]) {
      return values[key]
    } else {
      return ''
    }
  }
  const handleSubmit = event => {
    event.preventDefault()
    let data = {
      phoneNumber: getValue('phoneNumber'),
      email: getValue('email'),
      password: getValue('password'),
      firstName: getValue('firstName'),
      lastName: getValue('lastName'),
      role: [
        getValue('role')
      ]
    }

    const sellerInfo = {
      locality: getValue('locality'),
      OrgName: getValue('OrgName'),
      EmployeeId: getValue('EmployeeId'),
      postalCode: getValue('postalCode'),
      town: getValue('town'),
      country: getValue('country'),
      dotNumber: getValue('dotNumber'),
      proLicence: getValue('proLicence'),
      insurancepolicy: getValue('insurancepolicy'),
      logo: getValue('logo'),
      region: getValue('region'),
      streetAddress: getValue('streetAddress'),
      SSN: getValue('SSN'),
      DOB: getValue('DOB'),
      sellerBankingDetails: {
        accountTitle: getValue('accountTitle'),
        bankName: getValue('bankName'),
        accountNumber: getValue('accountNumber'),
        routingNumber: getValue('routingNumber')
      }
    }
    if (values.role === 'seller') {
      data = {
        ...data,
        sellerInfo: {
          ...sellerInfo
        }
      }
    }
    props.onCreateUser(data)
  }
  let loading = null
  let button = null
  if (props.response.loading) {
    loading = (
      <div>
        <LinearProgress />
        <br />
      </div>
    )
  } else {
    button = (
      <Button
        className={classes.saveButton}
        type="submit"
        variant="contained"
      >
        Create User
      </Button>
    )
  }
  let responseMessage = null
  if (props.response.error) {
    responseMessage = (
      <Alert
        message={props.response.errorMessage}
        variant="error"
      />
    )
  } else if (props.response.success) {
    responseMessage = (
      <Alert
        message={props.response.successMessage}
        variant="success"
      />
    )
  }
  let sellerInfoForm = null
  if (values.role === 'seller') {
    sellerInfoForm = (
      <React.Fragment>
        <SellerInfoForm
          conflicts={props.response.conflicts}
          handleChange={handleChange}
          loading={props.response.loading}
          values={values}
        />
        <SellerBankingForm
          conflicts={props.response.conflicts}
          handleChange={handleChange}
          loading={props.response.loading}
          values={values}
        />
      </React.Fragment>
    )
  }
  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      {responseMessage}
      <form onSubmit={handleSubmit}>
        {loading}
        <UserForm
          conflicts={props.response.conflicts}
          handleChange={handleChange}
          loading={props.response.loading}
          values={values}
        />
        {sellerInfoForm}
        <Divider />
        <CardActions>
          {button}
        </CardActions>
      </form>
    </Card>
  )
}

UserCreateForm.propTypes = {
  className: PropTypes.string
}

const mapStateToProps = state => {
  return {
    response: state.users.create
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onCreateUser: (data) => dispatch(actions.createUser(data))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(UserCreateForm)
