import React from 'react'
import {
  CardContent,
  CardHeader,
  Grid,
  TextField,
  Divider
} from '@material-ui/core'
import moment from 'moment'

const SellerInfoForm = (props) => {
  const sellerInfoFormFields = {
    address_details: {
      name: 'Address Details',
      fields: {
        locality: {
          grid: {
            md: 12,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Locality',
            name: 'locality'
          }
        },

        streetAddress: {
          grid: {
            md: 12,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Street Address',
            name: 'streetAddress'
          }

        },
        town: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Town',
            name: 'town'
          }
        },
        state: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'State',
            name: 'state'
          }
        },
        country: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'select',
            placeholder: 'Country',
            name: 'country',
            options: [
              {
                key: 'pak',
                value: 'Pakistan'
              }
            ]
          }
        },
        postalCode: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Postal Code',
            name: 'postalCode'
          }
        }
      }
    },
    OrdDetails: {
      name: 'Organization Details',
      fields: {
        OrgName: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Organization Name',
            name: 'OrgName'
          }
        },
        EmployeeId: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Employee Id',
            name: 'EmployeeId'
          }
        },
        logo: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'file',
            placeholder: 'Logo',
            name: 'logo'
          }
        }
      }
    },
    legal_details: {
      name: 'Legal Details',
      fields: {
        dotNumber: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Dot Number',
            name: 'dotNumber'
          }
        },
        proLicence: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Professional Licence',
            name: 'proLicence'
          }
        },
        insurancepolicy: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Insurance Policy',
            name: 'insurancepolicy'
          }
        },
        region: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'Region',
            name: 'region'
          }
        },
        SSN: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'text',
            placeholder: 'SSN',
            name: 'SSN'
          }
        },
        DOB: {
          grid: {
            md: 6,
            xs: 12
          },
          input: {
            type: 'date',
            placeholder: 'Date of Birth',
            name: 'DOB'
          }
        }
      }
    }

  }
  const getInputField = (grid, formValues, input) => {
    if (input.type === 'text') {
      return (
        <Grid
          item
          md={grid.md}
          xs={grid.xs}
        >
          <TextField
            disabled={props.loading}
            fullWidth
            label={input.placeholder}
            name={input.name}
            onChange={props.handleChange}
            type={input.type}
            value={formValues[input.name]}
            variant="outlined"
          />
        </Grid>
      )
    } else if (input.type === 'file') {
      return (
        <Grid
          item
          md={grid.md}
          xs={grid.xs}
        >
          <TextField
            disabled={props.loading}
            fullWidth
            label={input.placeholder}
            name={input.name}
            onChange={props.handleChange}
            type={input.type}
            value={formValues[input.name]}
            variant="outlined"
          />
        </Grid>
      )
    } else if (input.type === 'select') {
      return (
        <Grid
          item
          md={grid.md}
          xs={grid.xs}
        >
          <TextField
            fullWidth
            label={input.placeholder}
            name={input.name}
            onChange={props.handleChange}
            select
            // eslint-disable-next-line react/jsx-sort-props
            SelectProps={{ native: true }}
            value={formValues[input.name] || input.options[0]}
            variant="outlined"
          >
            {input.options.map(option => (
              <option
                key={option.key}
                value={option.key}
              >
                {option.value}
              </option>
            ))}
          </TextField>
        </Grid>
      )
    } else if (input.type === 'date') {
      return (
        <Grid
          item
          md={grid.md}
          xs={grid.xs}
        >
          <TextField
            disabled={props.loading}
            fullWidth
            label={input.placeholder}
            name={input.name}
            onChange={props.handleChange}
            type={input.type}
            value={formValues[input.name]}
            variant="outlined"
          />
        </Grid>
      )
    }
  }

  return (
    <React.Fragment>
      {Object.keys(sellerInfoFormFields).map(sectionKey => {
        const section = sellerInfoFormFields[sectionKey]
        const fields = section.fields
        const name = section.name
        return (
          <React.Fragment>
            <CardHeader title={name} />
            <Divider />
            <CardContent>
              <Grid
                container
                spacing={4}
              >
                {Object.keys(fields).map(fieldKey => {
                  const field = fields[fieldKey]
                  return getInputField(field.grid, props.values, field.input)
                })}
              </Grid>
            </CardContent>
          </React.Fragment>
        )
      })}
    </React.Fragment>
  )
}

export default SellerInfoForm
