import React from 'react'
import {
  CardContent,
  CardHeader,
  FormControl,
  FormControlLabel,
  FormLabel,
  Grid,
  Radio,
  RadioGroup,
  TextField
} from '@material-ui/core'

const UserForm = (props) => {
  const hasError = (key) => {
    if (props.conflicts) {
      if (props.conflicts[key]) {
        return true
      } else {
        return false
      }
    } else {
      return false
    }
  }
  const getErrorMessage = (key) => {
    if (props.conflicts) {
      if (props.conflicts[key]) {
        return props.conflicts[key]
      } else {
        return ''
      }
    } else {
      return ''
    }
  }
  return (
    <React.Fragment>
      <CardHeader title="Create User" />
      <CardContent>
        <Grid
          container
          spacing={4}
        >
          <Grid
            item
            md={6}
            xs={12}
          >
            <TextField
              disabled={props.loading}
              error={hasError('firstName')}
              fullWidth
              helperText={getErrorMessage('firstName')}
              label="First name"
              name="firstName"
              onChange={props.handleChange}
              required
              value={props.values.firstName}
              variant="outlined"
            />
          </Grid>
          <Grid
            item
            md={6}
            xs={12}
          >
            <TextField
              disabled={props.loading}
              error={hasError('lastName')}
              fullWidth
              helperText={getErrorMessage('lastName')}
              label="Last name"
              name="lastName"
              onChange={props.handleChange}
              required
              value={props.values.lastName}
              variant="outlined"
            />
          </Grid>
          <Grid
            item
            md={6}
            xs={12}
          >
            <TextField
              disabled={props.loading}
              error={hasError('email')}
              fullWidth
              helperText={getErrorMessage('email')}
              label="Email Address"
              name="email"
              onChange={props.handleChange}
              required
              type="email"
              value={props.values.email}
              variant="outlined"
            />
          </Grid>
          <Grid
            item
            md={6}
            xs={12}
          >
            <TextField
              disabled={props.loading}
              error={hasError('phoneNumber')}
              fullWidth
              helperText={getErrorMessage('phoneNumber')}
              label="Phone Number"
              name="phoneNumber"
              onChange={props.handleChange}
              required
              type="text"
              value={props.values.phoneNumber}
              variant="outlined"
            />
          </Grid>
          <Grid
            item
            md={12}
            xs={12}
          >
            <TextField
              disabled={props.loading}
              fullWidth
              label="Password"
              name="password"
              onChange={props.handleChange}
              required
              type="password"
              value={props.values.password}
              variant="outlined"
            />
          </Grid>
          <Grid
            item
            md={12}
            xs={12}
          >
            <FormControl component="fieldset">
              <FormLabel component="legend">Select User Type</FormLabel>
              <RadioGroup
                aria-label="role"
                name="role"
                onChange={props.handleChange}
                row
                value={props.values.role}
              >
                <FormControlLabel
                  control={<Radio
                    color="primary"
                    required
                  />}
                  disabled={props.loading}
                  label="Buyer"
                  labelPlacement="end"
                  value="buyer"
                />
                <FormControlLabel
                  control={<Radio
                    color="primary"
                    required
                  />}
                  disabled={props.loading}
                  label="Seller"
                  labelPlacement="end"
                  value="seller"
                />
              </RadioGroup>
            </FormControl>
          </Grid>
        </Grid>
      </CardContent>
    </React.Fragment>
  )
}

export default UserForm
